export default {
	name: "trailerStar",
	props: {
		innerScore: {
			type: Number,
			default: 0
		},
		isShowInnerScore: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			yellowNum: null,
			greyNum: null
		}
	},
	created () {
		if (this.innerScore !== null && this.innerScore !== undefined && this.innerScore !== '') {
			this.yellowNum = parseInt(this.innerScore / 2)
			this.greyNum = 5 - this.yellowNum 
		}
	}
}
